import React, { useState } from 'react'
import {Box,Typography,Collapse,FormGroup,FormControlLabel,Checkbox,Button,IconButton,Slider,MenuItem,Select,ListItemIcon,ListItemText,Grid,Drawer,ThemeProvider, createTheme} from '@mui/material'
import ShoppingCartIcon from '@mui/icons-material/ShoppingCart'
import SortByAlphaIcon from '@mui/icons-material/SortByAlpha'
import ArrowUpwardIcon from '@mui/icons-material/ArrowUpward'
import ArrowDownwardIcon from '@mui/icons-material/ArrowDownward'
import ExpandMoreIcon from '@mui/icons-material/ExpandMore'
import ExpandLessIcon from '@mui/icons-material/ExpandLess'
import DeleteSweepIcon from '@mui/icons-material/DeleteSweep'
import FilterListIcon from '@mui/icons-material/FilterList'
import CloseIcon from '@mui/icons-material/Close'
import Pagination from '@mui/material/Pagination'
import Card from '../../Components/Card/Card'
import { useUser } from '../../Context/Provider'
import LoadingPage from '../../Components/LoadingPage/LoadingPage'
import Alerts from '../../Components/Alerts/Alert'
import useProducts from './useProducts'

function ViewProducts() {
  const { dataContext } = useUser()
  const { state, handlers } = useProducts()
  const [openDrawer, setOpenDrawer] = useState(false)
  const [openCollapse, setOpenCollapse] = useState(null)

  const theme = createTheme({
    breakpoints: {
      values: {
        xs: 0,
        sm: 600,
        md: 1213,
        lg: 1380,
        xl: 1920,
      },
    },
  })  

  return (
    <ThemeProvider theme={theme}>
      <Box>
      {/* Pantalla de carga */}
      {state.isLoading && ( <LoadingPage /> )}

      {/* Alert para errores */}
      {state.success && (
          <Alerts 
            prop={{severity: "success", title: "Completado", alert: state.success}}  
            closeFunction={state.setSuccess} 
            params={null}
          /> 
        )}
        {state.warning && (
          <Alerts 
            prop={{severity: "warning", title: "Advertencia", alert: state.warning}}  
            closeFunction={state.setWarning} 
            params={null}
          /> 
        )}
        {state.error && (
          <Alerts 
            prop={{severity: "error", title: "Error", alert: state.error}}  
            closeFunction={state.setError} 
            params={null}
          /> 
        )}

        {/* Contador y Ordenar */}
        <Box
          display="flex"
          flexDirection="row"
          flexWrap="wrap"
          p={2}
          gap={1}
          sx={{ backgroundColor: '#e4ebee', justifyContent:"space-between", alignItems:"center" }}>
          <Box display="flex" sx={{ alignItems:"center" }} gap={1}>
            <ShoppingCartIcon />
            <Typography>{state.filteredProducts.length} Productos</Typography>
          </Box>
          <Box display="flex" sx={{ alignItems:"center" }} gap={2}>
            <Typography variant="body2">Ordenar Por:</Typography>
            <Select
              value={state.sortOrder}
              onChange={(e) => state.setSortOrder(e.target.value)}
              displayEmpty
              renderValue={(value) => {
                switch (value) {
                  case 'name-asc':
                    return (
                      <Box display="flex" sx={{ alignItems:"center" }} gap={1}>
                        <SortByAlphaIcon />
                        <Typography>Nombre Ascendente</Typography>
                      </Box>
                    )
                  case 'name-desc':
                    return (
                      <Box display="flex" sx={{ alignItems:"center" }} gap={1}>
                        <SortByAlphaIcon />
                        <Typography>Nombre Descendente</Typography>
                      </Box>
                    )
                  case 'price-asc':
                    return (
                      <Box display="flex" sx={{ alignItems:"center" }} gap={1}>
                        <ArrowDownwardIcon />
                        <Typography>Menor Precio</Typography>
                      </Box>
                    )
                  case 'price-desc':
                    return (
                      <Box display="flex" sx={{ alignItems:"center" }} gap={1}>
                        <ArrowUpwardIcon />
                        <Typography>Mayor Precio</Typography>
                      </Box>
                    )
                  default:
                    return <Typography>Opciones</Typography>
                }
              }}>
              <MenuItem value="name-asc">
                <ListItemIcon>
                  <SortByAlphaIcon />
                </ListItemIcon>
                <ListItemText primary="Nombre Ascendente" />
              </MenuItem>
              <MenuItem value="name-desc">
                <ListItemIcon>
                  <SortByAlphaIcon />
                </ListItemIcon>
                <ListItemText primary="Nombre Descendente" />
              </MenuItem>
              <MenuItem value="price-asc">
                <ListItemIcon>
                  <ArrowDownwardIcon />
                </ListItemIcon>
                <ListItemText primary="Menor Precio" />
              </MenuItem>
              <MenuItem value="price-desc">
                <ListItemIcon>
                  <ArrowUpwardIcon />
                </ListItemIcon>
                <ListItemText primary="Mayor Precio" />
              </MenuItem>
            </Select>
          </Box>
        </Box>

        {/* Botn Filtros para XS */}
        <Box display={{ xs: 'flex', md: 'none' }} sx={{ justifyContent:"flex-start", alignItems:"center" }} gap={1}>
          <Button
            variant="outlined"
            startIcon={<FilterListIcon />}
            onClick={() => setOpenDrawer(true)}
            sx={{ my: 2, mx: 'auto' }}>
            Filtros...
          </Button>
        </Box>

        {/* Drawer de Filtros */}
        <Drawer
          width="100%"
          height="100%"
          display="flex"      
          sx={{ justifyContent:"center", alignItems:"center" }}
          anchor="bottom"
          open={openDrawer}
          onClose={() => setOpenDrawer(false)}>
          <Box
            width="80vw"
            p={2}
            role="presentation"
            display="flex"
            flexDirection="column"
            sx={{ mx: 'auto' }}
            gap={2}>
            {/* Header del Drawer */}
            <Box display="flex" sx={{ justifyContent:"space-between", alignItems:"center" }}>
              <Typography variant="h6">Filtros</Typography>
              <IconButton onClick={() => setOpenDrawer(false)}>
                <CloseIcon />
              </IconButton>
            </Box>

            <Box>
              {/* Filtro por Marca */}
              <Box>
                <Box
                  display="flex"                
                  sx={{ cursor: 'pointer', justifyContent:"space-between", alignItems:"center" }}
                  onClick={() => setOpenCollapse(openCollapse === 'brand' ? null : 'brand')}>
                  <Typography variant="body1">Marca</Typography>
                  <IconButton size="small">
                    {openCollapse === 'brand' ? <ExpandLessIcon /> : <ExpandMoreIcon />}
                  </IconButton>
                </Box>
                <Collapse in={openCollapse === 'brand'}>
                  <FormGroup>
                    {[...new Set(dataContext.map((product) => product.brand))].map((brand) => (
                      <FormControlLabel
                        key={brand}
                        control={
                          <Checkbox
                            checked={state.selectedBrands.includes(brand)}
                            onChange={() => handlers.handleBrandChange(brand)}
                          />
                        }
                        label={brand}
                      />
                    ))}
                  </FormGroup>
                </Collapse>
              </Box>
            </Box>

            <Box>
              <Box>
                <Box
                  display="flex"                
                  sx={{ cursor: 'pointer', justifyContent:"space-between", alignItems:"center" }}
                  onClick={() => setOpenCollapse(openCollapse === 'category' ? null : 'category')}>
                  <Typography variant="body1">Categora</Typography>
                  <IconButton size="small">
                    {openCollapse === 'category' ? <ExpandLessIcon /> : <ExpandMoreIcon />}
                  </IconButton>
                </Box>
                <Collapse in={openCollapse === 'category'}>
                  <FormGroup>
                    {[...new Set(dataContext.map((product) => product.category))].map((category) => (
                      <FormControlLabel
                        key={category}
                        control={
                          <Checkbox
                            checked={state.selectedCategories.includes(category)}
                            onChange={() => handlers.handleCategoryChange(category)}
                          />
                        }
                        label={category}
                      />
                    ))}
                  </FormGroup>
                </Collapse>
              </Box>
            </Box>

            <Box>
              {/* Filtro por Rango de Precio */}
              <Typography variant="body1" gutterBottom mt={2}>
                Rango de Precio
              </Typography>
              <Slider
                value={state.priceRange}
                onChange={(e, newValue) => handlers.handlePriceChange(e, newValue)}
                onChangeCommitted={(e, newValue) => handlers.handlePriceChange(e, newValue)}
                valueLabelDisplay="auto"
                valueLabelFormat={(value) => `$${handlers.formatPrice(value)}`}
                step={10000}
                min={Math.min(...dataContext.map((p) => p.price))}
                max={Math.max(...dataContext.map((p) => p.price))}
              />
              <Box display="flex" justifyContent="space-between">
                <Typography variant="body2">${handlers.formatPrice(state.priceRange[0])}</Typography>
                <Typography variant="body2">${handlers.formatPrice(state.priceRange[1])}</Typography>
              </Box>
            </Box>

            {/* Botn Limpiar */}
            <Button
              sx={{
                width: '50%',
                mx: 'auto'
              }}
              variant="contained"
              color="primary"
              startIcon={<DeleteSweepIcon />}
              onClick={handlers.handleClearFilters}>
              Limpiar Todo
            </Button>
          </Box>
        </Drawer>

        {/* Contenido */}
        <Grid container justifyContent="center" sx={{ 
          px: { xs: 2, md: 10 }, 
          py : {xs: 2, md: 5} }}>
          {/* Filtros */}
          <Grid item="true" xs={12} md={4}>
            <Box width={'75%'}
              display={{ xs: 'none', md: 'block' }}
              mb={{ xs: 2, md: 0 }}
              p={2}
              border="1px solid #ddd"
              borderRadius="8px"
              sx={{
                boxSizing: 'border-box',
                position: { md: 'sticky' },
                top: { md: '100px' }
              }}
              zIndex={1}>
              <Typography variant="h6" gutterBottom>
                Filtros
              </Typography>

              {/* Filtro por Marca */}
              <Box>
                <Box
                  display="flex"                    
                  sx={{ cursor: 'pointer', alignItems:"center", justifyContent:"space-between" }}
                  onClick={() => setOpenCollapse(openCollapse === 'brand' ? null : 'brand')}>
                  <Typography variant="body1">Marca</Typography>
                  <IconButton size="small">
                    {openCollapse === 'brand' ? <ExpandLessIcon /> : <ExpandMoreIcon />}
                  </IconButton>
                </Box>
                <Collapse in={openCollapse === 'brand'}>
                  <FormGroup>
                    {[...new Set(dataContext.map((product) => product.brand))].map((brand) => (
                      <FormControlLabel
                        key={brand}
                        control={
                          <Checkbox
                            checked={state.selectedBrands.includes(brand)}
                            onChange={() => handlers.handleBrandChange(brand)}
                          />
                        }
                        label={brand}
                      />
                    ))}
                  </FormGroup>
                </Collapse>
              </Box>

              {/* Filtro por Categora */}
              <Box mt={2}>
                <Box
                  display="flex"
                  sx={{ cursor: 'pointer', alignItems:"center", justifyContent:"space-between" }}
                  onClick={() => setOpenCollapse(openCollapse === 'category' ? null : 'category')}>
                  <Typography variant="body1">Categora</Typography>
                  <IconButton size="small">
                    {openCollapse === 'category' ? <ExpandLessIcon /> : <ExpandMoreIcon />}
                  </IconButton>
                </Box>
                <Collapse in={openCollapse === 'category'}>
                  <FormGroup>
                    {[...new Set(dataContext.map((product) => product.category))].map((category) => (
                      <FormControlLabel
                        key={category}
                        control={
                          <Checkbox
                            checked={state.selectedCategories.includes(category)}
                            onChange={() => handlers.handleCategoryChange(category)}
                          />
                        }
                        label={category}
                      />
                    ))}
                  </FormGroup>
                </Collapse>
              </Box>

              {/* Filtro por Rango de Precio */}
              <Typography variant="body1" gutterBottom mt={2}>
                Rango de Precio
              </Typography>
              <Slider
                value={state.priceRange}
                onChange={(e, newValue) => handlers.handlePriceChange(e, newValue)}
                onChangeCommitted={(e, newValue) => handlers.handlePriceChange(e, newValue)}
                valueLabelDisplay="auto"
                valueLabelFormat={(value) => `$${handlers.formatPrice(value)}`}
                step={10000}
                min={Math.min(...dataContext.map((p) => p.price))}
                max={Math.max(...dataContext.map((p) => p.price))}
              />
              <Box display="flex" justifyContent="space-between">
                <Typography variant="body2">${handlers.formatPrice(state.priceRange[0])}</Typography>
                <Typography variant="body2">${handlers.formatPrice(state.priceRange[1])}</Typography>
              </Box>

              {/* Botn Limpiar Todo */}
              <Box mt={2}>
                <Button variant="contained" fullWidth color="primary" startIcon={<DeleteSweepIcon />} onClick={handlers.handleClearFilters}>
                  Limpiar Todo
                </Button>
              </Box>
            </Box>
          </Grid>

          {/* Productos */}
          <Grid item="true" xs={12} md={8}>
            <Box>
              {state.displayedProducts.length > 0 ? (
                <Grid container>
                  {state.displayedProducts.map((product) => (
                    
                    <Grid item="true" xs={12} md={6} xl={4} key={product.id}>
                      <Box sx={{ mx: 2, mb: 4 }}>
                        <Card product={product} setIsLoading={state.setIsLoading} setSuccess={state.setSuccess} setError={state.setError}/>
                      </Box>
                    </Grid>
                  ))}
                </Grid>
                ) : (
                <Box
                  display="flex"
                  sx={{ alignItems:"center", justifyContent:"center" }}
                  height="200px">
                  <Typography variant="h6" color="textSecondary">
                    No hay productos que coincidan con los filtros aplicados.
                  </Typography>
                </Box>
              )}

              {/* Paginacin */}
              {state.displayedProducts.length > 0 && (
                <Box mt={4} display="flex" justifyContent="center">
                    <Pagination
                    count={Math.ceil(state.filteredProducts.length / state.itemsPerPage)}
                    page={state.currentPage}
                    onChange={handlers.handlePageChange}
                    color="primary"
                    />
                </Box>
              )}
            </Box>
          </Grid>
        </Grid>
      </Box>
    </ThemeProvider>
  )
}

export default ViewProducts
