import React from "react"
import { AppBar, Toolbar, IconButton, Typography, Menu, MenuItem, Box, InputBase, Avatar, Drawer, List, ListItem, ListItemIcon, ListItemText, Divider, Grid } from "@mui/material"
import { Link } from "react-router-dom"
import MenuIcon from "@mui/icons-material/Menu"
import ComputerIcon from "@mui/icons-material/Computer"
import ShoppingCartIcon from "@mui/icons-material/ShoppingCart"
import HeadsetMicIcon from "@mui/icons-material/HeadsetMic"
import ContactSupportIcon from "@mui/icons-material/ContactSupport"
import SearchIcon from "@mui/icons-material/Search"
import InventoryIcon from "@mui/icons-material/Inventory"
import SettingsIcon from "@mui/icons-material/Settings"
import LogoutIcon from "@mui/icons-material/Logout"
import FavoriteIcon from '@mui/icons-material/Favorite'
import HistoryIcon from '@mui/icons-material/History'
import BusinessIcon from '@mui/icons-material/Business'
import NotificationsIcon from '@mui/icons-material/Notifications'
import CloseIcon from '@mui/icons-material/Close'
import Badge from "@mui/material/Badge"
import useNavBar from "./useNavBar"
import language from '../../Resources/Json/Languages/Es.json'
import "../../Resources/Css/Dashboard.css"

const NavBar = () => {
  const { state, handlers } = useNavBar()
  
  return (
    <AppBar position="sticky" className="gamer-navbar">
      <Toolbar>
        <Grid container spacing={2} display="flex" justifyContent="center">
          <Grid item lg={1}>
            <Typography variant="h6" component="div" className="navbar-title">Gamer Store</Typography>
          </Grid>

          <Grid item lg={3} sx={{ display: { xs: "none", sm: "none", md: "none", lg: "flex" } }}>
            <Box className="navbar-search">
              <Box className="search-icon"><SearchIcon /></Box>
              <InputBase placeholder={language.lblSearch} inputProps={{ "aria-label": "search" }} className="search-input"/>
            </Box>
          </Grid>

          <Grid item lg={6} sx={{ display: { xs: "none", sm: "none", md: "none", lg: "flex" } }}>
            {/* Men horizontal en dispositivos grandes */}
            <Box className="menu-horizontal">
              <Typography
                variant="button"
                component={Link}
                to="/home"
                className={`menu-link ${ state.location === "/home" ? "menu-active" : "" }`}>
              <ComputerIcon className={`menu-icon ${ state.location === "/home" ? "icon-active" : "" }`} /> Inicio
              </Typography>
              <Typography
                variant="button"
                component={Link}
                to="/home/products"
                className={`menu-link ${ state.location === "/home/products" ? "menu-active" : "" }`}>
              <InventoryIcon className={`menu-icon ${ state.location === "/home/products" ? "menu-active" : "" }`}/> Productos
              </Typography>
              <Typography
                variant="button"
                component={Link}
                to="/home/services"
                className={`menu-link ${ state.location === "/home/services" ? "menu-active" : "" }`}>
              <HeadsetMicIcon className={`menu-icon ${ state.location === "/home/services" ? "menu-active" : "" }`} /> Servicios
              </Typography>
              <Typography
                variant="button"
                component={Link}
                className="menu-link"
                onClick={handlers.scrollToFooter}>
              <ContactSupportIcon className="menu-icon" /> Contacto
              </Typography>
            </Box>
          </Grid>

          <Grid item lg={2}>            
            <Box display="flex" justifyContent="center">
              {/* Men hamburguesa */}
              <Box sx={{ display: { xs: "block", sm: "flex", md: "flex", lg: "none" } }}>
                <IconButton color="inherit" onClick={handlers.toggleDrawer(true)}>
                  <MenuIcon />
                </IconButton>
              </Box>

              <Drawer anchor="left" open={state.drawerOpen} onClose={handlers.toggleDrawer(false)}>
                <Box sx={{ height: '100%', backgroundColor: '#29354c' }}>
                  <Box sx={{ width: 250 }} role="presentation" onClick={handlers.toggleDrawer(false)} onKeyDown={handlers.toggleDrawer(false)}>              
                    <List>
                      <ListItem button component={Link} to="/home" className={`menu-link ${state.location === "/home" ? "menu-active" : ""}`}>
                        <ListItemIcon>
                          <ComputerIcon className={`menu-icon ${state.location === "/home" ? "menu-active" : ""}`}/>
                        </ListItemIcon>
                        <ListItemText primary="Inicio"  className={`menu-icon ${state.location === "/home" ? "menu-active" : ""}`} />
                      </ListItem>
                      <ListItem button component={Link} to="/home/products" className={`menu-link ${state.location === "/home/products" ? "menu-active" : ""}`}>
                        <ListItemIcon>
                          <InventoryIcon className={`menu-icon ${state.location === "/home/products" ? "menu-active" : ""}`} />
                        </ListItemIcon>
                        <ListItemText primary="Productos" className={`menu-icon ${state.location === "/home/products" ? "menu-active" : ""}`} />
                      </ListItem>
                      <ListItem button component={Link} to="/home/services" className={`menu-link ${state.location === "/home/services" ? "menu-active" : ""}`}>
                        <ListItemIcon>
                          <HeadsetMicIcon className={`menu-icon ${state.location === "/home/services" ? "menu-active" : ""}`} />
                        </ListItemIcon>
                        <ListItemText primary="Servicios" className={`menu-icon ${state.location === "/home/services" ? "menu-active" : ""}`} />
                      </ListItem>
                      <ListItem button component={Link} onClick={handlers.scrollToFooter} className={`menu-link`}>
                        <ListItemIcon>
                          <ContactSupportIcon className={`menu-icon`} />
                        </ListItemIcon>
                        <ListItemText primary="Contacto" className={`menu-icon`} />
                      </ListItem>
                    </List>
                    <Box className="navbar-search-burger">
                      <Box className="search-icon-burger">
                        <SearchIcon />
                      </Box>
                      <InputBase
                        placeholder={language.lblSearch}
                        inputProps={{ "aria-label": "search" }}
                        className="search-input"
                      />
                    </Box>
                  </Box>
                </Box>
              </Drawer>  

              <Box className="cart-icon">
                <IconButton component={Link} to="/home/favorites">
                  <Badge badgeContent={state.favoritesCount} color="secondary">
                    <FavoriteIcon sx={{ color: state.location === '/home/favorites' ? 'red' : 'white' }} />
                  </Badge>
                </IconButton>
              </Box>

              {/* cono del carrito con contador */}
              <Box className="cart-icon">
                <IconButton component={Link} to="/home/cart">
                  <Badge badgeContent={state.cartCount} color="secondary">
                    <ShoppingCartIcon sx={{ color: state.location === '/home/cart' ? 'red' : 'white' }} />
                  </Badge>
                </IconButton>
              </Box>

              {/* cono de notificaciones con contador */}
              <Box className="notification-icon">
                <IconButton onClick={() => handlers.setDrawerNotifyOpen(true)}>
                  <Badge badgeContent={state.notifyCount} color="error">
                    <NotificationsIcon sx={{ color: state.location === '/home/notifications' ? 'red' : 'white' }} />
                  </Badge>
                </IconButton>
              </Box>

              {/* Drawer para las notificaciones */}
              <Drawer anchor="right" open={state.drawerNotifyOpen} onClose={() => handlers.setDrawerNotifyOpen(false)} sx={{ '& .MuiDrawer-paper': { width: 300 }}}>
                <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', p: 2 }}>
                  <Box sx={{ display: 'flex', alignItems: 'center' }}>
                    <NotificationsIcon color="primary" sx={{ mr: 1 }} />
                    <Typography variant="h6">Notificaciones</Typography>
                  </Box>
                  <IconButton onClick={() => handlers.setDrawerNotifyOpen(false)}>
                    <CloseIcon />
                  </IconButton>
                </Box>
                <Divider />

                <List>
                  {state.notifications.length > 0 ? (
                    state.notifications.map((notification) => (
                      <ListItem
                        key={notification.id}
                        sx={{ mb: 2,
                          display: 'flex',
                          justifyContent: 'space-between',
                          alignItems: 'center',
                        }}>
                        <Box>
                          <Typography variant="subtitle1" fontWeight="bold">
                            {notification.title}
                          </Typography>
                          <Typography variant="body2" color="text.secondary">
                            {notification.description}
                          </Typography>
                        </Box>
                        <IconButton onClick={() => handlers.removeNotification(notification.id)}>
                          <CloseIcon fontSize="small" />
                        </IconButton>
                      </ListItem>
                    ))
                  ) : (
                    <Typography variant="body2" align="center" sx={{ mt: 2, color: 'text.secondary' }}>
                      No hay notificaciones nuevas.
                    </Typography>
                  )}
                </List>
              </Drawer>   

              <Box className="user-avatar">
                <IconButton onClick={handlers.handleUserMenuOpen}>
                  <Avatar alt="Usuario" src={state.token?.user?.picture || "/img/avatar.png"} referrerPolicy="no-referrer" />
                </IconButton>
                <Menu
                  anchorEl={state.userMenuAnchorEl}
                  open={state.isUserMenuOpen}
                  onClose={handlers.handleUserMenuClose}
                  anchorOrigin={{
                    vertical: "top",
                    horizontal: "right",
                  }}
                  transformOrigin={{
                    vertical: "top",
                    horizontal: "right",
                  }}>
                    
                  <MenuItem onClick={handlers.handleUserMenuClose} component={Link} to="/home/settings">
                    <SettingsIcon style={{ marginRight: "10px" }} />Configuraciones
                  </MenuItem>
                  <MenuItem onClick={handlers.handleUserMenuClose} component={Link} to="/home/history">
                    <HistoryIcon style={{ marginRight: "10px" }} />Historial
                  </MenuItem>
                  <MenuItem onClick={handlers.handleUserMenuClose} component={Link} to="/home/company">
                    <BusinessIcon style={{ marginRight: "10px" }} />Nosotros
                  </MenuItem>
                  <MenuItem onClick={handlers.handleUserMenuClose} component={Link} to="/">
                    <LogoutIcon style={{ marginRight: "10px" }} />Cerrar Sesin
                  </MenuItem>
                </Menu>
              </Box>
            </Box>
          </Grid>
        </Grid>                                      
      </Toolbar>
      {/* Lnea animada multicolor */}
      <div className="animated-line" />
    </AppBar>
  )
}

export default NavBar
