import { useState } from 'react'
import { useUser } from '../../Context/Provider'

const useProducts = () => {
    const { dataContext } = useUser()
    const [currentPage, setCurrentPage] = useState(1)
    const itemsPerPage = 6

    const [openBrand, setOpenBrand] = useState(false)
    const [openCategory, setOpenCategory] = useState(false)
    const [selectedBrands, setSelectedBrands] = useState([])
    const [selectedCategories, setSelectedCategories] = useState([])
    const [priceRange, setPriceRange] = useState([Math.min(...dataContext.map((p) => p.price)), Math.max(...dataContext.map((p) => p.price))])
    const [sortOrder, setSortOrder] = useState('')
    const [isLoading, setIsLoading] = useState(false)
    const [success, setSuccess] = useState(null)
    const [warning, setWarning] = useState(null)
    const [error, setError] = useState(null)

    const formatPrice = (value) => {
        return value.toLocaleString('es-ES', { minimumFractionDigits: 2, maximumFractionDigits: 2 })
    }

    const handlePageChange = (event, value) => { 
        setCurrentPage(value) 
        window.scrollTo({
            top: 0,
            behavior: "smooth"
        })
    }
    const handleBrandChange = brand => { setSelectedBrands((prev) => prev.includes(brand) ? prev.filter((b) => b !== brand) : [...prev, brand]) }
    const handleCategoryChange = category => { setSelectedCategories((prev) => prev.includes(category) ? prev.filter((c) => c !== category) : [...prev, category]) }
    const handlePriceChange = (event, newValue) => { 
          
        setPriceRange(newValue) 
    }
    const handleClearFilters = () => {
        setSelectedBrands([])
        setSelectedCategories([])
        setPriceRange([Math.min(...dataContext.map((p) => p.price)), Math.max(...dataContext.map((p) => p.price))])
        setSortOrder('')
    }
    const sortProducts = (dataContext) => {
        switch (sortOrder) {
            case 'name-asc': return dataContext.sort((a, b) => a.name.localeCompare(b.name))
            case 'name-desc': return dataContext.sort((a, b) => b.name.localeCompare(a.name))
            case 'price-asc': return dataContext.sort((a, b) => a.price - b.price)
            case 'price-desc': return dataContext.sort((a, b) => b.price - a.price)
            default: return dataContext
        }
    }
    const filteredProducts = dataContext.filter((product) => {
        const matchesBrand = selectedBrands.length ? selectedBrands.includes(product.brand) : true
        const matchesCategory = selectedCategories.length ? selectedCategories.includes(product.category) : true
        const matchesPrice = product.price >= priceRange[0] && product.price <= priceRange[1]
        return matchesBrand && matchesCategory && matchesPrice
    })
    const sortedProducts = sortProducts(filteredProducts)
    const displayedProducts = sortedProducts.slice((currentPage - 1) * itemsPerPage, currentPage * itemsPerPage)

    const state = {
        currentPage,
        openBrand,
        openCategory,
        selectedBrands,
        selectedCategories,
        priceRange,
        sortOrder,
        filteredProducts,
        displayedProducts,
        setOpenBrand,
        setOpenCategory,
        setSortOrder,
        itemsPerPage,
        isLoading,
        setIsLoading,
        success,
        setSuccess,
        warning,
        setWarning,
        error,
        setError
    }
    
      const handlers = {
        handlePageChange,
        handleBrandChange,
        handleCategoryChange,
        handlePriceChange,
        handleClearFilters,
        formatPrice
    }

    return { state, handlers }
}

export default useProducts