import React, { useState } from 'react'
import {
  Container,
  Typography,
  Box,
  Button,
  TextField,
  IconButton,
  Grid,
} from '@mui/material'
import ArrowBackIcon from '@mui/icons-material/ArrowBack'

const Services = () => {
  const [selectedService, setSelectedService] = useState(null)

  const servicios = [
    {
      id: 1,
      titulo: 'Reparaciones de Notebooks',
      descripcion: 'Realizamos reparaciones de hardware y software para notebooks.',
      imagen: 'https://picsum.photos/600/300?random=1',
    },
    {
      id: 2,
      titulo: 'Actualizacin de Notebooks',
      descripcion: 'Actualizamos tu notebook, como cambiar HDD a SSD o agregar memoria RAM.',
      imagen: 'https://picsum.photos/600/300?random=1',
    },
  ]

  const handleServiceClick = (service) => {
    setSelectedService(service)
    window.scrollTo({
      top: 0,
      behavior: "smooth"
    })
  }

  const handleBack = () => {
    setSelectedService(null)
  }

  return (
    <Container sx={{ my: 7, px: { xs: 2, sm: 10, md: 25, xl: 30 } }}>
      {selectedService ? (
        <Box>
          <IconButton
            onClick={handleBack}
            sx={{ position: 'absolute', top: 100, left: 8 }}
          >
            <ArrowBackIcon />
          </IconButton>
          <Typography variant="h4" gutterBottom>
            {selectedService.titulo}
          </Typography>
          <Typography variant="body1" gutterBottom>
            {selectedService.descripcion}
          </Typography>
          <Box component="form" mt={2}>
            <TextField
              fullWidth
              label="Marca de notebook"
              margin="normal"
              variant="outlined"
            />
            <TextField
              fullWidth
              label="Modelo (opcional)"
              margin="normal"
              variant="outlined"
            />
            <TextField
              fullWidth
              label="Detalles del Servicio"
              margin="normal"
              variant="outlined"
              multiline
              rows={4}
              defaultValue={`Solicitud para: ${selectedService.titulo}`}
            />
            <Button
              variant="contained"
              color="primary"
              size="large"
              type="submit"
              fullWidth
            >
              Enviar Solicitud
            </Button>
          </Box>
        </Box>
      ) : (
        <Box>
          <Typography
            variant="h4"
            gutterBottom
            align="center"
            sx={{ fontWeight: 'bold', textTransform: 'uppercase', color: '#3f51b5' }}
          >
            Nuestros Servicios
          </Typography>
          <Grid container spacing={3}>
            {servicios.map((servicio) => (
              <Grid item xs={12} md={6} key={servicio.id}>
                <Box
                  onClick={() => handleServiceClick(servicio)}
                  sx={{
                    cursor: 'pointer',
                    border: '1px solid #ddd',
                    borderRadius: '8px',
                    overflow: 'hidden',
                    transition: '0.3s',
                    '&:hover': { boxShadow: '0 4px 12px rgba(0, 0, 0, 0.1)' },
                  }}
                >
                  <img
                    src={servicio.imagen}
                    alt={servicio.titulo}
                    style={{ width: '100%', display: 'block' }}
                  />
                  <Typography
                    variant="h6"
                    align="center"
                    sx={{ padding: 1, backgroundColor: '#f5f5f5' }}
                  >
                    {servicio.titulo}
                  </Typography>
                </Box>
              </Grid>
            ))}
          </Grid>
        </Box>
      )}
    </Container>
  )
}

export default Services
