import React from "react"
import "../../Resources/Css/Carrusel.css"
import { IconButton } from "@mui/material"
import ChevronLeftIcon from '@mui/icons-material/ChevronLeft'
import ChevronRightIcon from '@mui/icons-material/ChevronRight'
import FiberManualRecordIcon from "@mui/icons-material/FiberManualRecord"
import useCarrusel from "./useCarrusel"

const ImageCarousel = () => {
  const { state, handlers, providerLocal } = useCarrusel()

  return (
    <div className="carousel-container">
      {/* Imgenes */}
      <div className="carousel-images" style={{ transform: `translateX(-${state.currentIndex * 100}%)` }}>
        {providerLocal.images.map((image, index) => (
          <img
            key={index}
            src={image}
            alt={`Imagen ${index + 1}`}
            className="carousel-image"
          />
        ))}
      </div>

      {/* Flechas */}
      <IconButton onClick={handlers.handlePrev} className="carousel-arrow carousel-arrow-left">
        <ChevronLeftIcon />
      </IconButton>
      <IconButton onClick={handlers.handleNext} className="carousel-arrow carousel-arrow-right">
        <ChevronRightIcon />
      </IconButton>

      {/* Indicadores (Puntos) */}
      <div className="carousel-indicators">
        {providerLocal.images.map((_, index) => (
          <IconButton key={index} onClick={() => handlers.handleDotClick(index)} className="carousel-dot">
            <FiberManualRecordIcon
              className={
                state.currentIndex === index
                  ? "carousel-dot-active"
                  : "carousel-dot-inactive"
              }
            />
          </IconButton>
        ))}
      </div>
    </div>
  )
}

export default ImageCarousel
