import React from 'react'
import Carrusel from '../../Components/Carrusel/Carrusel'
import { Box, Grid2, Typography, Accordion, AccordionSummary, AccordionDetails, Container, ThemeProvider, createTheme } from '@mui/material'
import useDashboard from './useDashboard'
import CardCarrusel from '../../Components/Carrusel/CardCarrusel'
import ExpandMoreIcon from '@mui/icons-material/ExpandMore'
import "../../Resources/Css/Dashboard.css"
import { useUser } from '../../Context/Provider'
import LoadingPage from '../../Components/LoadingPage/LoadingPage'
import Alerts from '../../Components/Alerts/Alert'

const Dashboard = () => {
  const { state, features } = useDashboard()
  const { dataContext } = useUser()

  const theme = createTheme({
    breakpoints: {
      values: {
        xs: 0,
        sm: 600,
        md: 1213,
        lg: 1380,
        xl: 1920,
      }
    }
  })    

  return (
    <>
      <ThemeProvider theme={theme}>

      {/* Pantalla de carga */}
      {state.isLoading && ( <LoadingPage /> )}

      {/* Alert para errores */}
      {state.success && (
        <Alerts 
          prop={{severity: "success", title: "Completado", alert: state.success}}  
          closeFunction={state.setSuccess} 
          params={null}
        /> 
      )}
      {state.warning && (
        <Alerts 
          prop={{severity: "warning", title: "Advertencia", alert: state.warning}}  
          closeFunction={state.setWarning} 
          params={null}
        /> 
      )}
      {state.error && (
        <Alerts 
          prop={{severity: "error", title: "Error", alert: state.error}}  
          closeFunction={state.setError} 
          params={null}
        /> 
      )}

      <Carrusel />
      <br />
      <Typography variant="h4" align="center" gutterBottom sx={{ fontWeight: 'bold', color: '#00004e', my: 2 }}>Top 10 mas vendidos</Typography>
      <CardCarrusel cards={dataContext} setIsLoading={state.setIsLoading} setSuccess={state.setSuccess} setError={state.setError}/>
      <br />
      <Box className="features-container">
        <Grid2 container spacing={10} justifyContent="center">
          {features.map((feature, index) => (
            <Grid2
              item
              xs={12}
              sm={6}
              md={4}
              key={index}
              className="feature-item">
              <Box className="feature-icon">{feature.icon}</Box>
              <Typography variant="h6" className="feature-title">
                {feature.title}
              </Typography>
              <Typography variant="body2" className="feature-description">
                {feature.description}
              </Typography>
            </Grid2>
          ))}
        </Grid2>
      </Box>
      <br />
      <CardCarrusel cards={dataContext} />
      <br />
      <Container maxWidth="md" sx={{ mt: 4, mb: 4 }}>
        <Typography variant="h4" align="center" gutterBottom sx={{ fontWeight: 'bold', color: '#00004e' }}>Preguntas Frecuentes</Typography>
        <Box mt={2}>
          <Accordion>
            <AccordionSummary expandIcon={<ExpandMoreIcon />} aria-controls="panel1-content" id="panel1-header">
              <Typography variant="h6">Qu servicios ofrecen?</Typography>
            </AccordionSummary>
            <AccordionDetails>
              <Typography>
                Ofrecemos servicios de ecommerce, venta de productos de informtica, 
                cursos especializados y soporte tcnico para notebooks.
              </Typography>
            </AccordionDetails>
          </Accordion>

          <Accordion>
            <AccordionSummary expandIcon={<ExpandMoreIcon />} aria-controls="panel2-content" id="panel2-header">
              <Typography variant="h6">Cmo puedo realizar una compra?</Typography>
            </AccordionSummary>
            <AccordionDetails>
              <Typography>
                Puedes realizar tu compra a travs de nuestro sitio web agregando productos 
                al carrito y completando el proceso de pago de forma segura.
              </Typography>
            </AccordionDetails>
          </Accordion>

          <Accordion>
            <AccordionSummary expandIcon={<ExpandMoreIcon />} aria-controls="panel3-content" id="panel3-header">
              <Typography variant="h6">Tienen servicio de soporte tcnico?</Typography>
            </AccordionSummary>
            <AccordionDetails>
              <Typography>
                S, ofrecemos soporte tcnico para notebooks. Puedes agendar un turno a 
                travs de nuestro sistema online para recibir atencin personalizada.
              </Typography>
            </AccordionDetails>
          </Accordion>
        </Box>
      </Container>
      <br />
      <br />
      </ThemeProvider>
    </>
  )
}

export default Dashboard
