import { useState, useRef, useEffect } from 'react'
import { useTheme } from '@mui/material/styles'
import { useMediaQuery } from '@mui/material'
import SwapHorizIcon from '@mui/icons-material/SwapHoriz'
import PaymentsIcon from '@mui/icons-material/Payments'
import CreditCardIcon from '@mui/icons-material/CreditCard'
import StoreIcon from '@mui/icons-material/Store'
import AttachMoneyIcon from '@mui/icons-material/AttachMoney'
import LocalShippingIcon from '@mui/icons-material/LocalShipping'

const useCart = () => {
    const [page, setPage] = useState(0)
    const [rowsPerPage, setRowsPerPage] = useState(5)
    const theme = useTheme()
    const isMobile = useMediaQuery(theme.breakpoints.down('sm'))
    const tableRef = useRef(null)
    const [shouldScroll, setShouldScroll] = useState(false)
    const [totalItems, setTotalItems] = useState(0)
    const [total, setTotal] = useState(0)
    const [categorySummary, setCategorySummary] = useState({})
    const [deliveryOption, setDeliveryOption] = useState('')
    const [selectedPayment, setSelectedPayment] = useState('')
    const scrollToTable = () => { tableRef.current && window.scrollTo({ top: 0, behavior: 'smooth' }) }
    const [cartItems, setCartItems] = useState([
        {
        id: 1,
        image: 'https://picsum.photos/50/50?random=1',
        name: 'Teclado Mecnico',
        category: 'Perifericos',
        price: 12300,
        quantity: 1,
        },
        {
        id: 2,
        image: 'https://picsum.photos/50/50?random=1',
        name: 'Mouse Gamer',
        category: 'Perifericos',
        price: 30000,
        quantity: 1,
        },
        {
            id: 3,
            image: 'https://picsum.photos/50/50?random=1',
            name: 'Auricular JBL',
            category: 'Perifericos',
            price: 71000,
            quantity: 1,
        },
        {
            id: 4,
            image: 'https://picsum.photos/50/50?random=1',
            name: 'Parlante JBL',
            category: 'Perifericos',
            price: 1000000,
            quantity: 1,
        },
        {
            id: 5,
            image: 'https://picsum.photos/50/50?random=1',
            name: 'SSD',
            category: 'Almacenamiento',
            price: 58500,
            quantity: 1,
        },
        {
            id: 6,
            image: 'https://picsum.photos/50/50?random=1',
            name: 'Iphone 16 pro max',
            category: 'Iphones',
            price: 1700000,
            quantity: 1,
        }
    ])

    const paymentMethods = [
        { id: 'cash', label: 'Efectivo', icon: <AttachMoneyIcon /> },
        { id: 'card', label: 'Debido/Credito', icon: <CreditCardIcon /> },
        { id: 'transfer', label: 'Transferencia', icon: <SwapHorizIcon /> },
        { id: 'app', label: 'Mercado Pago', icon: <PaymentsIcon /> },
    ]

    const deliveryMethods = [
        { id: 'local', label: 'Retirar en local', icon: <StoreIcon /> },
        { id: 'domicilio', label: 'Envo a domicilio', icon: <LocalShippingIcon /> },
    ]

    const handleRemove = (id) => {
        console.log(`Eliminar producto con ID: ${id}`)
    }

    const handleChangePage = (event, newPage) => {
        event.preventDefault()
        setPage(newPage)
        setShouldScroll(true)
    }

    const handleChangeRowsPerPage = (event) => {
        event.preventDefault()
        setRowsPerPage(parseInt(event.target.value, 10))
        setPage(0)
        setShouldScroll(true)
    }

    const paginatedItems = cartItems.slice(
        page * rowsPerPage,
        page * rowsPerPage + rowsPerPage
    )

    const [quantities, setQuantities] = useState(
        cartItems.reduce((acc, item) => {
        acc[item.id] = item.quantity
        return acc
        }, {})
    )

    const handleQuantityChange = (id, newQuantity) => {
        setQuantities((prevQuantities) => ({
        ...prevQuantities,
        [id]: newQuantity,
        }))
    }
    
    useEffect(() => { if (shouldScroll) {
        scrollToTable()
        setShouldScroll(false)
        }
    }, [shouldScroll])

    useEffect(() => {
        const newTotal = cartItems.reduce((sum, item) => {return sum + item.price * (quantities[item.id] || 0)}, 0)
        setTotal(newTotal)
    }, [quantities, cartItems])

    useEffect(() => {
        const calculatedTotalItems = cartItems.reduce((sum, item) => {return sum + (quantities[item.id] || 0)}, 0)
        setTotalItems(calculatedTotalItems)
    }, [quantities, cartItems])  

    useEffect(() => {
        const newCategorySummary = cartItems.reduce((acc, item) => {
            const category = item.category || 'Otros'
            acc[category] = (acc[category] || 0) + (quantities[item.id] || 0)
        return acc
        }, {})
    
        setCategorySummary(newCategorySummary)
    }, [quantities, cartItems])

    const state = {
        page,
        rowsPerPage,
        isMobile,
        tableRef,
        totalItems,
        total,
        categorySummary,
        cartItems,
        selectedPayment,
        setSelectedPayment,
        paymentMethods,
        deliveryOption,
        setDeliveryOption,
        deliveryMethods,
        paginatedItems,
        quantities
    }
    
      const handlers = {
        handleRemove,
        handleChangePage,
        handleChangeRowsPerPage,
        handleQuantityChange
    }

    return { state, handlers }
}

export default useCart