import { useState } from 'react'

const useCarrusel = () => {
    const [currentIndex, setCurrentIndex] = useState(0)
    const handleNext = () => { setCurrentIndex((prevIndex) => prevIndex === images.length - 1 ? 0 : prevIndex + 1) }
    const handlePrev = () => { setCurrentIndex((prevIndex) => prevIndex === 0 ? images.length - 1 : prevIndex - 1) }
    const handleDotClick = (index) => { setCurrentIndex(index) }

    const images = [
        "https://picsum.photos/1920/400?random=1",
        "https://picsum.photos/1920/400?random=2",
        "https://picsum.photos/1920/400?random=3"
    ]

    const state = {
        currentIndex
    }
    const handlers = {
        handleNext,
        handlePrev,
        handleDotClick
    }

    const providerLocal = {
        images
    }

    return { state, handlers, providerLocal }
}

export default useCarrusel