import React from "react"
import { Container, Typography, Grid, Card, CardContent, Box, Breadcrumbs, Link } from "@mui/material"
import { Security, Info, People } from "@mui/icons-material"

const Company = () => {
  return (
    <Container maxWidth="lg" sx={{ py: 5 }}>
      <Breadcrumbs aria-label="breadcrumb" sx={{ marginBottom: 2 }}>
        <Link href="/home" color="inherit" underline="hover">Inicio</Link>
        <Typography color="textPrimary">Nosotros</Typography>
      </Breadcrumbs>

      {/* Ttulo principal */}
      <Typography
        variant="h3"
        align="center"
        gutterBottom
        sx={{ fontWeight: "bold", color: "primary.main" }}
      >
        Sobre Nosotros
      </Typography>

      {/* Introduccin */}
      <Typography
        variant="body1"
        align="center"
        sx={{ mb: 4, color: "text.secondary" }}
      >
        Somos una empresa dedicada a la tecnologa, comprometida con ofrecer los
        mejores productos y servicios, priorizando siempre la seguridad y
        satisfaccin de nuestros usuarios.
      </Typography>

      {/* Seccin de tarjetas informativas */}
      <Grid container spacing={4}>
        {/* Informacin de la empresa */}
        <Grid item xs={12} sm={6} md={4}>
          <Card elevation={3}>
            <CardContent>
              <Box
                sx={{
                  display: "flex",
                  justifyContent: "center",
                  mb: 2,
                }}
              >
                <Info fontSize="large" color="primary" />
              </Box>
              <Typography variant="h5" align="center" gutterBottom>
                Quines Somos
              </Typography>
              <Typography variant="body2" align="center" color="text.secondary">
                Fundada en el 2020, nuestra empresa se especializa en ecommerce,
                soporte tcnico y cursos. Creemos en la innovacin constante y
                el crecimiento sostenido.
              </Typography>
            </CardContent>
          </Card>
        </Grid>

        {/* Seguridad de datos */}
        <Grid item xs={12} sm={6} md={4}>
          <Card elevation={3}>
            <CardContent>
              <Box
                sx={{
                  display: "flex",
                  justifyContent: "center",
                  mb: 2,
                }}
              >
                <Security fontSize="large" color="primary" />
              </Box>
              <Typography variant="h5" align="center" gutterBottom>
                Seguridad de Datos
              </Typography>
              <Typography variant="body2" align="center" color="text.secondary">
                Implementamos protocolos avanzados para proteger la informacin
                de nuestros usuarios, cumpliendo con los estndares ms altos
                de la industria.
              </Typography>
            </CardContent>
          </Card>
        </Grid>

        {/* Equipo */}
        <Grid item xs={12} sm={6} md={4}>
          <Card elevation={3}>
            <CardContent>
              <Box
                sx={{
                  display: "flex",
                  justifyContent: "center",
                  mb: 2,
                }}
              >
                <People fontSize="large" color="primary" />
              </Box>
              <Typography variant="h5" align="center" gutterBottom>
                Nuestro Equipo
              </Typography>
              <Typography variant="body2" align="center" color="text.secondary">
                Contamos con un equipo apasionado, altamente capacitado y
                enfocado en brindar un servicio excepcional a nuestros clientes.
              </Typography>
            </CardContent>
          </Card>
        </Grid>
      </Grid>

      {/* Declaracin de misin */}
      <Box sx={{ mt: 6 }}>
        <Typography
          variant="h4"
          align="center"
          sx={{ fontWeight: "bold", color: "primary.main" }}
          gutterBottom
        >
          Nuestra Misin
        </Typography>
        <Typography
          variant="body1"
          align="center"
          sx={{ color: "text.secondary" }}
        >
          Innovar y liderar en el mercado tecnolgico, garantizando la mejor
          experiencia para nuestros clientes mientras cuidamos la seguridad y
          privacidad de sus datos.
        </Typography>
      </Box>
    </Container>
  )
}

export default Company
