import React from 'react'
import useCardCarrusel from './useCardCarrusel'
import Card from '../Card/Card'
import ChevronLeftIcon from '@mui/icons-material/ChevronLeft'
import ChevronRightIcon from '@mui/icons-material/ChevronRight'
import { Box, Grid, IconButton } from "@mui/material"

const CardCarrusel = ({ cards, setIsLoading, setSuccess, setError }) => {
    const { state, handlers } = useCardCarrusel()

    return (
        <Box className="carrusel-container">
            {/* Flecha izquierda */}
            {!(state.currentIndex === 0) && (
                <IconButton onClick={handlers.handlePrev} className="carrusel-button left"><ChevronLeftIcon /></IconButton>
            )}

            {/* Carrusel */}
            <Grid container className="carrusel-grid" style={{transform: `translateX(-${state.currentIndex * 100}%)`}}>

            {cards.map((card, index) => (
                <Grid item="true" key={index} xs={12} sm={6} md={4} xl={3} className="carrusel-card" style={{flex: `0 0 calc(100% / ${state.cardsPerView})`,  boxSizing: "border-box"}}>
                    <Card product={card} setIsLoading={setIsLoading} setSuccess={setSuccess} setError={setError} sx={{ padding: 2, textAlign: "center" }}>Card {card}</Card>
                </Grid>
            ))}

            </Grid>

            {/* Flecha derecha */}
            {!(state.currentIndex + 1 >= Math.ceil(cards.length / state.cardsPerView)) && (
                <IconButton onClick={() => handlers.handleNext(cards.length)} className="carrusel-button right"><ChevronRightIcon /></IconButton>
            )}
        </Box>
    )
}

export default CardCarrusel