import React from 'react'
import {Table,TableBody,TableCell,TableContainer,TableHead,TableRow,Paper,IconButton,Typography,Button,Box,Grid,TablePagination,Breadcrumbs,Link,TextField,List,ListItem,ListItemText} from '@mui/material'
import DeleteIcon from '@mui/icons-material/Delete'
import ShoppingCartIcon from '@mui/icons-material/ShoppingCart'
import LocalOfferIcon from '@mui/icons-material/LocalOffer'
import AttachMoneyIcon from '@mui/icons-material/AttachMoney'
import ShoppingBagIcon from '@mui/icons-material/ShoppingBag'
import CalculateIcon from '@mui/icons-material/Calculate'
import SettingsIcon from '@mui/icons-material/Settings'
import PaymentIcon from '@mui/icons-material/Payment'
import Helper from '../../Resources/Class/helper'
import useCart from '../Cart/useCart'

const Cart = () => {
  const { state, handlers } = useCart()

  return (
    <Box sx={{ p: 4 }}>
      <Breadcrumbs aria-label="breadcrumb" sx={{ marginBottom: 2 }}>
        <Link href="/home" color="inherit" underline="hover">Inicio</Link>
        <Link href="/home/products" color="inherit" underline="hover">Productos</Link>
        <Typography color="textPrimary">Carrito</Typography>
      </Breadcrumbs>

      <Typography variant="h4" gutterBottom sx={{
            color: '#3F51B5',
            fontFamily: 'Roboto, sans-serif',
            fontWeight: 700,
            textAlign: 'center'
        }}>
        Tu carrito <ShoppingCartIcon />
      </Typography>

      <Grid container spacing={4}>
        <Grid item xs={12} md={8}>
          <TableContainer ref={state.tableRef} component={Paper} sx={{ boxShadow: '0px 4px 10px rgba(0, 0, 0, 0.2)' }}>
            <Table>
              <TableHead>
                <TableRow>
                  <TableCell sx={{ width: '20%' }}>
                    <Box display="flex" flexDirection="column" alignItems="center"> <ShoppingBagIcon sx={{ marginRight: 1 }} /> Producto </Box>
                  </TableCell>
                  {!state.isMobile && (
                    <TableCell sx={{ width: '20%' }}>
                      <Box display="flex" flexDirection="column" alignItems="center"> <AttachMoneyIcon sx={{ marginRight: 1 }} /> Precio Unitario </Box>
                    </TableCell>
                  )}
                  <TableCell sx={{ width: '20%' }}>
                    <Box display="flex" flexDirection="column" alignItems="center"> <LocalOfferIcon sx={{ marginRight: 1 }} /> Cantidad </Box>
                  </TableCell>
                  <TableCell sx={{ width: '20%' }}>
                    <Box display="flex" flexDirection="column" alignItems="center"> <CalculateIcon sx={{ marginRight: 1 }} /> Subtotal </Box>
                  </TableCell>
                  <TableCell sx={{ width: '20%' }}>
                    <Box display="flex" flexDirection="column" alignItems="center"> <SettingsIcon sx={{ marginRight: 1 }} /> Acciones </Box>
                  </TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
              {state.paginatedItems.map((item, index) => (
                <TableRow key={item.id} sx={{ backgroundColor: index % 2 === 0 ? '#ffffff' : '#f5f5f5' }}>
                  <TableCell>
                    <Box display="flex" alignItems="center" justifyContent="center">
                      <Box component="img"
                        src={item.image}
                        alt={item.name}
                        sx={{ width: 50, height: 50, marginRight: 1 }}/>
                    </Box>
                  </TableCell>
                  {!state.isMobile && (
                    <TableCell>
                      <Box display="flex" alignItems="center" justifyContent="center"> ${Helper.formatPrice(item.price)} </Box>
                    </TableCell>
                  )}
                  <TableCell align="center">
                    <Box display="flex" alignItems="center" justifyContent="center">
                      <TextField
                        value={state.quantities[item.id]}
                        variant="outlined"
                        size="small"
                        inputProps={{
                          style: { textAlign: 'center', width: 40, height: 30 },
                        }}
                        sx={{ mx: 1 }}
                        readOnly/>
                      <Box display="flex" flexDirection="column" alignItems="center">
                        <Button variant="outlined"
                          color="primary"
                          size="small"
                          onClick={() => handlers.handleQuantityChange(item.id, state.quantities[item.id] + 1)}
                          sx={{ minWidth: 30, padding: 0 }}>
                          +
                        </Button>
                        <Button variant="outlined"
                          color="primary"
                          size="small"
                          onClick={() => handlers.handleQuantityChange(item.id, state.quantities[item.id] - 1)}
                          disabled={state.quantities[item.id] === 1}
                          sx={{ minWidth: 30, padding: 0, mt: 0.5 }}>
                          -
                        </Button>
                      </Box>
                    </Box>
                  </TableCell>
                  <TableCell>
                    <Box display="flex" alignItems="center" justifyContent="center"> ${Helper.formatPrice(item.price * state.quantities[item.id])} </Box>
                  </TableCell>
                  <TableCell>
                    <Box display="flex" alignItems="center" justifyContent="center">
                      <IconButton color="error" onClick={() => handlers.handleRemove(item.id)}> <DeleteIcon /> </IconButton>
                    </Box>
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
            </Table>
          </TableContainer>
          <TablePagination
              rowsPerPageOptions={[5, 10, 15]}
              component="div"
              count={state.cartItems.length}
              rowsPerPage={state.rowsPerPage}
              page={state.page}
              onPageChange={handlers.handleChangePage}
              onRowsPerPageChange={handlers.handleChangeRowsPerPage}
              labelRowsPerPage="Filas por pgina:"
              labelDisplayedRows={({ from, to, count }) => `${from}-${to} de ${count}`}/>
        </Grid>

        <Grid item xs={12} md={4} sx={{
            position: { xs: 'static', md: 'sticky' },
            top: { md: 70 },
            alignSelf: 'start',
          }}>
          <Paper elevation={3} sx={{ p: 2 }}>
            <Box display="flex" mb={1}>
              <Typography variant="h6" gutterBottom my={0}> Resumen del carrito </Typography>
              <ShoppingCartIcon sx={{ my: 'auto', mx: 1 }}/>
            </Box>
            {/* Medios de Pago */}
            <Typography variant="body1" gutterBottom> Medios de Pago: </Typography>
            <Box display="flex" gap={2}>
              <Grid container spacing={1}>
                {state.paymentMethods.map((method) => (
                  <Grid item xs={3} sm={6} md={6} lg={3} key={method.id}>
                    <Box
                      key={method.id}
                      onClick={() => state.setSelectedPayment(method.id)}
                      sx={{
                        cursor: 'pointer',
                        display: 'flex',
                        flexDirection: 'column',
                        alignItems: 'center',
                        padding: 1,
                        borderRadius: 1,
                        backgroundColor: state.selectedPayment === method.id ? '#e0f7fa' : 'transparent',
                        '&:hover': {
                          backgroundColor: '#e0f2f1',
                        },
                      }}>
                      {method.icon}
                      <Typography variant="caption" align="center"> {method.label} </Typography>
                    </Box>
                  </Grid>
                ))}
              </Grid>
            </Box>
            {/* Opciones de Entrega */}
            <Typography variant="body1" gutterBottom> Opciones de Entrega: </Typography>
            <Box display="flex" gap={2}>
              {state.deliveryMethods.map((method) => (
                <Box
                  key={method.id}
                  onClick={() => state.setDeliveryOption(method.id)}
                  sx={{
                    cursor: 'pointer',
                    display: 'flex',
                    flexDirection: 'column',
                    alignItems: 'center',
                    padding: 1,
                    borderRadius: 1,
                    backgroundColor: state.deliveryOption === method.id ? '#e0f7fa' : 'transparent',
                    '&:hover': {
                      backgroundColor: '#e0f2f1',
                    },
                  }}>
                  {method.icon}
                  <Typography variant="caption" align="center"> {method.label} </Typography>
                </Box>
              ))}
            </Box>
            <Typography variant="body1" gutterBottom my={0}> Categoras en tu carrito: </Typography>
            <List sx={{ listStyleType: 'disc', pl: 4 }}>
              {Object.entries(state.categorySummary).map(([category, count]) => (
                  <ListItem key={category} sx={{ display: 'list-item', pl: 0, py: 0 }}>
                  <ListItemText primary={`${category}: ${count} producto(s)`} primaryTypographyProps={{ variant: 'body2' }}/>
                </ListItem>
              ))}
            </List>
            <Typography variant="body1" gutterBottom> Total de productos: <strong>{state.totalItems}</strong> </Typography>
            <Typography variant="body1" gutterBottom> Entrega seleccionada: <strong>{state.deliveryOption ? state.deliveryMethods.find((m) => m.id === state.deliveryOption).label : 'Ninguna'}</strong> </Typography>
            <Typography variant="body1" gutterBottom> Medio seleccionado: <strong>{state.selectedPayment ? state.paymentMethods.find((m) => m.id === state.selectedPayment).label : 'Ninguno'}</strong> </Typography>
            <Typography variant="body1" gutterBottom sx={{ color: 'green' }}> <strong>Total: ${Helper.formatPrice(state.total)}</strong> </Typography>
            <Button variant="contained" color="primary" fullWidth startIcon={<PaymentIcon />} onClick={() => console.log('Proceder con la compra')}> Finalizar compra </Button>
          </Paper>
        </Grid>
      </Grid>
    </Box>
  )
}

export default Cart
