import React from 'react'
import {Table,TableBody,TableCell,TableContainer,TableHead,TableRow,Paper,Typography,IconButton,Breadcrumbs,Link,Menu,MenuItem,ListItemIcon,ListItemText,Box,TablePagination} from '@mui/material'
import ShoppingCartIcon from '@mui/icons-material/ShoppingCart'
import VisibilityIcon from '@mui/icons-material/Visibility'
import AttachMoneyIcon from '@mui/icons-material/AttachMoney'
import ShoppingBagIcon from '@mui/icons-material/ShoppingBag'
import DescriptionIcon from '@mui/icons-material/Description'
import PriceChangeIcon from '@mui/icons-material/PriceChange'
import ActionsIcon from '@mui/icons-material/Build'
import MoreVertIcon from '@mui/icons-material/MoreVert'
import FavoriteIcon from '@mui/icons-material/Favorite'
import DeleteIcon from '@mui/icons-material/Delete'
import helper from '../../Resources/Class/helper'
import ModalInfo from '../../Components/Modal/ModalInfo'
import useFavorites from './useFavorites'

const Favorites = () => {
  const { state, handlers, context } = useFavorites()

  return (
    <Box padding={2}>
      <Breadcrumbs aria-label="breadcrumb" sx={{ marginBottom: 2 }}>
        <Link href="/home" color="inherit" underline="hover">Inicio</Link>
        <Link href="/home/products" color="inherit" underline="hover">Productos</Link>
        <Typography color="textPrimary">Favoritos</Typography>
      </Breadcrumbs>

      {/* Importar y usar el Modal */}
      <ModalInfo
        open={state.openModal}
        handleClose={handlers.handleCloseModal}
        product={state.selectedProductModal}
      />

      <Box sx={{ px: 2, py: 5 }}>
        <Typography
          variant="h4"
          gutterBottom
          sx={{
            color: '#3F51B5',
            fontFamily: 'Roboto, sans-serif',
            fontWeight: 700,
            textAlign: 'center',
          }}
        > Tus Favoritos <FavoriteIcon />
        </Typography>
        <TableContainer
          component={Paper}
          ref={context.tableRef}
          sx={{ boxShadow: '0px 4px 10px rgba(0, 0, 0, 0.2)' }}>

          <Table>
            <TableHead>
              <TableRow>
                <TableCell>
                  <Box display="flex" alignItems="center">
                    <ShoppingBagIcon sx={{ marginRight: 1 }} /> Producto
                  </Box>
                </TableCell>
                {!context.isMobile && (
                  <TableCell>
                    <Box display="flex" alignItems="center">
                      <DescriptionIcon sx={{ marginRight: 1 }} /> Descripcin
                    </Box>
                  </TableCell>
                )}
                <TableCell>
                  <Box display="flex" alignItems="center">
                    <PriceChangeIcon sx={{ marginRight: 1 }} /> Precio
                  </Box>
                </TableCell>
                <TableCell>
                  <Box display="flex" alignItems="center">
                    <ActionsIcon sx={{ marginRight: 1 }} /> Acciones
                  </Box>
                </TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {context.productosVisibles.map((product, index) => (
                <TableRow
                  key={product.id}
                  sx={{ backgroundColor: index % 2 === 0 ? '#ffffff' : '#f5f5f5' }}>

                  <TableCell>
                    <Box display="flex" alignItems="center">
                      <Box
                        component="img"
                        src={product.image}
                        alt={product.name}
                        sx={{ width: 50, height: 50, marginRight: 1 }}/>
                      {product.name}
                    </Box>
                  </TableCell>
                  {!context.isMobile && <TableCell>{product.description}</TableCell>}
                  <TableCell>${helper.formatPrice(product.price)}</TableCell>
                  <TableCell>
                    <Box display="flex" justifyContent="center">
                      <IconButton
                        aria-label="acciones"
                        onClick={event => handlers.handleMenuOpen(event, product.id)}
                        sx={{
                          backgroundColor: '#f5f5f5',
                          padding: 1,
                          borderRadius: 2,
                          boxShadow: 1,
                          '&:hover': { backgroundColor: '#e0e0e0' },
                        }}> <MoreVertIcon sx={{ color: '#3F51B5' }} />
                      </IconButton>
                    </Box>
                    <Menu
                      anchorEl={state.anchorEl}
                      open={Boolean(state.anchorEl) && state.selectedProduct === product.id}
                      onClose={handlers.handleMenuClose}>
                        
                      <MenuItem onClick={handlers.handleMenuClose}>
                        <ListItemIcon><AttachMoneyIcon fontSize="small" /></ListItemIcon>
                        <ListItemText>Comprar ahora</ListItemText>
                      </MenuItem>  
                      <MenuItem onClick={handlers.handleMenuClose}>
                        <ListItemIcon><ShoppingCartIcon fontSize="small" /></ListItemIcon>
                        <ListItemText>Aadir al carrito</ListItemText>
                      </MenuItem>
                      <MenuItem onClick={event => handlers.handleModalOpen(product)}>
                        <ListItemIcon><VisibilityIcon fontSize="small" /></ListItemIcon>
                        <ListItemText>Ver ms</ListItemText>
                      </MenuItem>
                      <MenuItem onClick={handlers.handleMenuClose}>
                        <ListItemIcon><DeleteIcon fontSize="small" /></ListItemIcon>
                        <ListItemText>Eliminar</ListItemText>
                      </MenuItem>
                    </Menu>
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </TableContainer>
        <TablePagination
          rowsPerPageOptions={[5, 10, 15]}
          component="div"
          count={context.dataContext.length}
          rowsPerPage={state.rowsPerPage}
          page={state.page}
          onPageChange={handlers.handleChangePage}
          onRowsPerPageChange={handlers.handleChangeRowsPerPage}
          labelRowsPerPage="Filas por pgina:"
          labelDisplayedRows={({ from, to, count }) => `${from}-${to} de ${count}`}
        />
      </Box>
    </Box>
  )
}

export default Favorites
