import React from 'react'
import { Box, Grid2, IconButton, Typography, Button, TextField } from "@mui/material"
import InstagramIcon from "@mui/icons-material/Instagram"
import MusicNoteIcon from "@mui/icons-material/MusicNote"
import SendIcon from "@mui/icons-material/Send"
import FacebookIcon from "@mui/icons-material/Facebook"
import YouTubeIcon from "@mui/icons-material/YouTube"
import "../../Resources/Css/Footer.css"

const Footer = () => {
    return (
        <>
        {/* Lnea animada multicolor */}
        <div className="animated-line-footer" />
        {/* Footer principal */}
        <Box className="footer">
            <Grid2 container spacing={4} justifyContent="center">
            {/* Formulario de contacto */}
            <Grid2 item="true" xs={12} md={6}>
                <Typography variant="h6" className="footer-title">
                Contctanos
                </Typography>
                <form>
                <TextField
                    fullWidth
                    label="Email"
                    type="email"
                    variant="outlined"
                    className="footer-input"
                    required
                />
                <TextField
                    fullWidth
                    label="Mensaje"
                    multiline
                    rows={4}
                    variant="outlined"
                    className="footer-input"
                    required
                />
                <Button
                    variant="contained"
                    color="secondary"
                    type="submit"
                    endIcon={<SendIcon />}
                    className="footer-button"
                >
                    Enviar
                </Button>
                </form>
            </Grid2>

            {/* Redes sociales */}
            <Grid2 item="true" xs={12} md={6} className="footer-social">
                <Typography variant="h6" className="footer-title">
                Sguenos en redes sociales
                </Typography>
                <IconButton
                href="https://www.facebook.com"
                target="_blank"
                rel="noopener noreferrer"
                className="social-icon"
                >
                <FacebookIcon className="icon" />
                </IconButton>

                <IconButton
                href="https://www.tiktok.com"
                target="_blank"
                rel="noopener noreferrer"
                className="social-icon"
                >
                <MusicNoteIcon className="icon" />
                </IconButton>

                <IconButton
                href="https://www.youtube.com"
                target="_blank"
                rel="noopener noreferrer"
                className="social-icon"
                >
                <YouTubeIcon className="icon" />
                </IconButton>

                <IconButton
                href="https://www.instagram.com"
                target="_blank"
                rel="noopener noreferrer"
                className="social-icon"
                >
                <InstagramIcon className="icon" />
                </IconButton>
            </Grid2>
            </Grid2>
        </Box>

        {/* Derechos reservados */}
        <Box className="footer-rights">
            <Typography variant="body2" className="footer-rights-text">
            Todos los derechos reservados  2024
            </Typography>
        </Box>
      </>
    )
}

export default Footer