import React from "react"
import {
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Paper,
  Typography,
  Chip,
  Box,
  Breadcrumbs,
  Link
} from "@mui/material"
import ShoppingCartIcon from "@mui/icons-material/ShoppingCart"
import DescriptionIcon from "@mui/icons-material/Description"
import AttachMoneyIcon from "@mui/icons-material/AttachMoney"
import CalendarTodayIcon from "@mui/icons-material/CalendarToday"
import HistoryIcon from "@mui/icons-material/History"
import CheckCircleOutlineIcon from "@mui/icons-material/CheckCircleOutline"

const purchases = [
  {
    id: 1,
    date: "2025-01-20",
    productImage: "https://via.placeholder.com/100",
    products: "Laptop, Mouse",
    description: "Laptop y Mouse de alta calidad",
    quantity: 2,
    total: "$1500",
    status: "Completed",
  },
  {
    id: 2,
    date: "2025-01-18",
    productImage: "https://via.placeholder.com/100",
    products: "Keyboard",
    description: "Teclado mecnico RGB",
    quantity: 1,
    total: "$100",
    status: "Pending",
  },
  {
    id: 3,
    date: "2025-01-15",
    productImage: "https://via.placeholder.com/100",
    products: "Headphones, Monitor",
    description: "Auriculares y monitor para gaming",
    quantity: 2,
    total: "$800",
    status: "Completed",
  },
]

const PurchaseHistory = () => {
  return (
    <Box sx={{ px: 4, py: 6 }}>
      <Breadcrumbs aria-label="breadcrumb" sx={{ marginBottom: 2 }}>
        <Link href="/home" color="inherit" underline="hover">Inicio</Link>
        <Typography color="textPrimary">Historial de compras</Typography>
      </Breadcrumbs>

      {/* Ttulo principal con cono */}
      <Box display="flex" alignItems="center" justifyContent="center" mb={3}>
        <HistoryIcon fontSize="large" sx={{ marginRight: 1, fontSize: 40, color: '#3F51B5' }} />
        <Typography variant="h4" gutterBottom sx={{
            color: '#3F51B5',
            fontFamily: 'Roboto, sans-serif',
            fontWeight: 700,
            textAlign: 'center',
            fontSize: { xs: 20, md: 30 },
            my: 0,
           }}>
          Historial de Compras
        </Typography>
      </Box>

      <TableContainer component={Paper} elevation={3}>
        <Table>
          <TableHead>
            <TableRow>
              <TableCell>
                <ShoppingCartIcon /> <strong>Producto</strong>
              </TableCell>
              <TableCell>
                <DescriptionIcon /> <strong>Descripcin</strong>
              </TableCell>
              <TableCell>
                <CalendarTodayIcon /> <strong>Fecha</strong>
              </TableCell>
              <TableCell>
                <AttachMoneyIcon /> <strong>Total</strong>
              </TableCell>
              <TableCell>
                <CheckCircleOutlineIcon /> <strong>Estado</strong>
              </TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {purchases.map((purchase, index) => (
              <TableRow key={purchase.id} sx={{ backgroundColor: index % 2 === 0 ? '#ffffff' : '#f5f5f5' }}>
                <TableCell>
                  <Box display="flex" alignItems="center">
                    <img
                      src={purchase.productImage}
                      alt="Producto"
                      style={{ width: 50, height: 50, marginRight: 10 }}
                    />
                  </Box>
                </TableCell>
                <TableCell>{purchase.description}</TableCell>
                <TableCell>{purchase.date}</TableCell>
                <TableCell>{purchase.total}</TableCell>
                <TableCell>
                  <Chip
                    label={purchase.status}
                    color={purchase.status === "Completed" ? "success" : "warning"}
                    variant="outlined"
                  />
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
    </Box>
  )
}

export default PurchaseHistory
