import React, { useState } from 'react';
import { Card, CardContent, Typography, Button, TextField, Grid, Breadcrumbs, Box, Link } from '@mui/material';
import { ShoppingCart } from '@mui/icons-material';
import AttachMoneyIcon from '@mui/icons-material/AttachMoney'
import SwapHorizIcon from '@mui/icons-material/SwapHoriz'
import PaymentsIcon from '@mui/icons-material/Payments'
import CreditCardIcon from '@mui/icons-material/CreditCard'
import PaymentIcon from '@mui/icons-material/Payment'
import StoreIcon from '@mui/icons-material/Store'
import LocalShippingIcon from '@mui/icons-material/LocalShipping'
import { useParams } from "react-router-dom"
import { useUser } from "../../Context/Provider"

const SingleProductPurchase = () => {
  const [quantity, setQuantity] = useState(1)
  const [selectedImage, setSelectedImage] = useState(null)
  const [selectedPayment, setSelectedPayment] = useState('')
  const { id } = useParams()
  const { dataContext } = useUser()
  const contextProduct = dataContext.find((item) => Number(item.id) === Number(id))

  const paymentMethods = [
    { id: 'cash', label: 'Efectivo', icon: <AttachMoneyIcon /> },
    { id: 'card', label: 'Debido/Credito', icon: <CreditCardIcon /> },
    { id: 'transfer', label: 'Transferencia', icon: <SwapHorizIcon /> },
    { id: 'app', label: 'Mercado Pago', icon: <PaymentsIcon /> },
  ]

  const [deliveryOption, setDeliveryOption] = useState('')

  const deliveryMethods = [
    { id: 'local', label: 'Retirar en local', icon: <StoreIcon /> },
    { id: 'domicilio', label: 'Envo a domicilio', icon: <LocalShippingIcon /> },
  ]

  const handleQuantityChange = (e) => {
    setQuantity(Number(e.target.value));
  };

  const handlePurchase = () => {
    console.log('Finalizing purchase with:');
  };

  const handleAddToCart = () => {
    console.log('Added to cart with:', { quantity });
  };

  return (
    <Card sx={{ maxWidth: 1200, m: '20px auto', p: '20px', boxShadow: {
        xs: 'none', 
        md: '0px 4px 10px rgba(0, 0, 0, 0.2)', 
      } }}>
      <Breadcrumbs aria-label="breadcrumb" sx={{ marginBottom: 4 }}>
        <Link href="/home" color="inherit" underline="hover">Inicio</Link>
        <Link href="/home/products" color="inherit" underline="hover">Productos</Link>
        <Typography color="textPrimary">Compra</Typography>
      </Breadcrumbs>
      <Grid container spacing={4}>
        {/* Main Product Image */}
        <Grid item xs={12} md={6}>
          <Box
            component="img"
            src={selectedImage || contextProduct.image}
            alt={contextProduct.name}
            sx={{ width: '100%', borderRadius: '8px' }}
          />
          <Grid container spacing={1} style={{ marginTop: '10px' }}>
            {contextProduct.images.map((image, index) => (
              <Grid item xs={2} key={index}>
                <Box
                key={index}
                component="img"
                src={image}
                alt={`Product ${index}`}
                sx={{
                    width: 60,
                    height: 60,
                    cursor: 'pointer',
                    border: selectedImage === image ? '2px solid blue' : '1px solid gray',
                    borderRadius: 1,
                }}
                onClick={() => setSelectedImage(image)}/>
              </Grid>
            ))}
          </Grid>
        </Grid>

        {/* Product Details */}
        <Grid item xs={12} md={6}>
          <Typography variant="h4" gutterBottom>
            {contextProduct.name}
          </Typography>
          <Typography variant="body1" gutterBottom>
            {contextProduct.description}
          </Typography>
          <Typography variant="h6" gutterBottom>
            ${contextProduct.price.toLocaleString()}
          </Typography>

          {/* Quantity Selector */}
          <Typography variant="subtitle1">Cantidad:</Typography>
          <Box display="flex" alignItems="center" sx={{ py: 2 }}>
            <TextField
              value={1}
              variant="outlined"
              size="small"
              inputProps={{
                style: { textAlign: 'center', width: 40, height: 30 },
              }}
              sx={{ mx: 1 }}
              readOnly/>
            <Box display="flex" flexDirection="column" alignItems="center">
              <Button variant="outlined"
                color="primary"
                size="small"
                // onClick={() => handlers.handleQuantityChange(item.id, state.quantities[item.id] + 1)}
                sx={{ minWidth: 30, padding: 0 }}>
                +
              </Button>
              <Button variant="outlined"
                color="primary"
                size="small"
                // onClick={() => handlers.handleQuantityChange(item.id, state.quantities[item.id] - 1)}
                disabled={2 === 1}
                sx={{ minWidth: 30, padding: 0, mt: 0.5 }}>
                -
              </Button>
            </Box>
          </Box>

          {/* Medios de Pago */}
          <Typography variant="body1" gutterBottom> Medios de Pago:</Typography>
            <Box display="flex" gap={2}>
              <Grid container spacing={1}>
                {paymentMethods.map((method) => (
                  <Grid item xs={3} sm={6} md={6} lg={3} key={method.id}>
                    <Box
                      key={method.id}
                      onClick={() => setSelectedPayment(method.id)}
                      sx={{
                        cursor: 'pointer',
                        display: 'flex',
                        flexDirection: 'column',
                        alignItems: 'center',
                        padding: 1,
                        borderRadius: 1,
                        backgroundColor: selectedPayment === method.id ? '#e0f7fa' : 'transparent',
                        '&:hover': {
                          backgroundColor: '#e0f2f1',
                        },
                      }}
                    >
                      {method.icon}
                      <Typography variant="caption" align="center">
                        {method.label}
                      </Typography>
                    </Box>
                  </Grid>
                ))}
              </Grid>
            </Box>
          <Typography variant="body1" gutterBottom mt={2}>
            Opciones de Entrega:
          </Typography>
          <Box display="flex" gap={2}>
            {deliveryMethods.map((method) => (
              <Box
                key={method.id}
                onClick={() => setDeliveryOption(method.id)}
                sx={{
                  cursor: 'pointer',
                  display: 'flex',
                  flexDirection: 'column',
                  alignItems: 'center',
                  padding: 1,
                  borderRadius: 1,
                  backgroundColor: deliveryOption === method.id ? '#e0f7fa' : 'transparent',
                  '&:hover': {
                    backgroundColor: '#e0f2f1',
                  },
                }}
              >
                {method.icon}
                <Typography variant="caption" align="center">
                  {method.label}
                </Typography>
              </Box>
            ))}
          </Box>
        <Typography variant="body1" gutterBottom mt={2}>
          Entrega seleccionada: <strong>{deliveryOption ? deliveryMethods.find((m) => m.id === deliveryOption).label : 'Ninguna'}</strong>
        </Typography>
        <Typography variant="body1" gutterBottom mt={1}>
            Medio seleccionado: <strong>{selectedPayment ? paymentMethods.find((m) => m.id === selectedPayment).label : 'Ninguno'}</strong>
        </Typography>

          {/* Action Buttons */}
          <Grid container spacing={2} style={{ marginTop: '20px' }}>
            <Grid item>
              <Button
                variant="contained"
                color="primary"
                startIcon={<PaymentIcon />}
                onClick={handlePurchase}
              >
                Finalizar Compra
              </Button>
            </Grid>
            <Grid item>
              <Button
                variant="outlined"
                color="primary"
                startIcon={<ShoppingCart />}
                onClick={handleAddToCart}
              >
                Aadir al Carrito
              </Button>
            </Grid>
          </Grid>
        </Grid>
      </Grid>

      {/* Technical Details */}
      <CardContent style={{ marginTop: '20px' }}>
        <Typography variant="h6">Ficha Tcnica:</Typography>
        <Typography variant="body2">{contextProduct.description}</Typography>
      </CardContent>
    </Card>
  );
};

export default SingleProductPurchase;
