import React, { useState, useEffect } from "react";
import { Modal, Box, Typography, IconButton, Button } from "@mui/material";
import CloseIcon from "@mui/icons-material/Close";
import ShoppingCartIcon from "@mui/icons-material/ShoppingCart";

const style = {
  position: "absolute",
  top: "50%",
  left: "50%",
  transform: "translate(-50%, -50%)",
  width: "90%",
  maxWidth: 500,
  bgcolor: "background.paper",
  boxShadow: 24,
  p: 4,
  borderRadius: 2,
};

const ModalInfo = ({ open, handleClose, product }) => {
  const [selectedImage, setSelectedImage] = useState(null);

  // Formatear el precio
  const formatPrice = (value) => {
    return value.toLocaleString("es-ES", { minimumFractionDigits: 2, maximumFractionDigits: 2 });
  };

  // Actualizar imagen seleccionada cuando cambia el producto
  useEffect(() => {
    if (product && product.images?.length > 0) {
      setSelectedImage(product.images[0]);
    } else {
      setSelectedImage(null);
    }
  }, [product]);

  // Render del modal con validacin
  return (
    <Modal open={open} onClose={handleClose} sx={{ mx: { xs: 4 } }}>
      <Box sx={style}>
        {product ? (
          <>
            <Box display="flex" justifyContent="space-between" mb={2}>
              <Typography variant="h6">{product.name}</Typography>
              <IconButton onClick={handleClose}>
                <CloseIcon />
              </IconButton>
            </Box>
            {selectedImage && (
              <img
                src={selectedImage}
                alt="Product"
                style={{ width: "100%", borderRadius: 8 }}
              />
            )}
            <Box mt={2} display="flex" gap={1}>
              {product.images.map((image, index) => (
                <img
                  key={index}
                  src={image}
                  alt={`Product ${index}`}
                  style={{
                    width: 60,
                    height: 60,
                    cursor: "pointer",
                    border: selectedImage === image ? "2px solid blue" : "1px solid gray",
                    borderRadius: 4,
                  }}
                  onClick={() => setSelectedImage(image)}
                />
              ))}
            </Box>
            <Typography variant="body1" mt={2}>
              {product.description}
            </Typography>
            <Typography variant="h6" mt={1}>
              ${formatPrice(product.price)}
            </Typography>
            <Button
              variant="contained"
              color="primary"
              fullWidth
              sx={{ mt: 2 }}
              startIcon={<ShoppingCartIcon />}
              onClick={() => alert("Comprar ahora")}
            >
              Comprar ahora
            </Button>
          </>
        ) : (
          <Typography variant="body2" color="textSecondary">
            Cargando informacin del producto...
          </Typography>
        )}
      </Box>
    </Modal>
  );
};

export default ModalInfo;
