import { useState, useEffect } from 'react'
import { useMediaQuery } from '@mui/material'
import { useTheme } from '@mui/material/styles'

const useCardCarrusel = () => {
    const [currentIndex, setCurrentIndex] = useState(0)
    const theme = useTheme()
    const isXs = useMediaQuery(theme.breakpoints.down("sm"))
    const isSm = useMediaQuery(theme.breakpoints.between("sm", "md"))
    const isMd = useMediaQuery(theme.breakpoints.up("md"))
    const isXl = useMediaQuery(theme.breakpoints.up("xl"))
    const cardsPerView = isXs ? 1 : isSm ? 2 : isMd ? 3 : 4

    const handleNext = quantity => {
        setCurrentIndex((prevIndex) =>
        prevIndex + 1 < Math.ceil(quantity / cardsPerView) ? prevIndex + 1 : prevIndex
        )
    }

    const handlePrev = () => {
        setCurrentIndex((prevIndex) => (prevIndex > 0 ? prevIndex - 1 : prevIndex))
    }

    useEffect(() => { setCurrentIndex(0) }, [isXs, isSm, isMd, isXl])

    const state = {
        currentIndex,
        cardsPerView
    }

    const handlers = {
        handleNext,
        handlePrev
    }

    return { state, handlers }
}

export default useCardCarrusel