import React from "react"
import { Fab, Box, TextField, Typography, Button } from "@mui/material"
import WhatsAppIcon from "@mui/icons-material/WhatsApp"
import CloseIcon from "@mui/icons-material/Close"
import InputMask from "react-input-mask"
import useWhatsApp from "./useWhatsapp"
import Alerts from "../Alerts/Alert"
import LoadingPage from "../LoadingPage/LoadingPage"
import "../../Resources/Css/Whatsapp.css"

const WhatsApp = () => {
  const { state, handlers } = useWhatsApp()

  return (
    <>
      {/* Pantalla de carga */}
      {state.isLoading && ( <LoadingPage /> )}

      {/* Alert para errores */}
      {state.success && (
        <Alerts 
          prop={{severity: "success", title: "Completado", alert: state.success}}  
          closeFunction={state.setSuccess} 
          params={null}
        /> 
      )}
      {state.error && (
        <Alerts 
          prop={{severity: "error", title: "Error", alert: state.error}}  
          closeFunction={state.setError} 
          params={null}
        /> 
      )}

      {/* Botn flotante */}
      <Box className="floating-container">
        {!state.isOpen && <Typography className="floating-text">Chatea con nosotros</Typography>}
        <Fab aria-label="whatsapp" onClick={handlers.handleToggle} className={`whatsapp-button ${state.isOpen ? "open" : ""}`}>
          {state.isOpen ? <CloseIcon /> : <WhatsAppIcon />}
        </Fab>
      </Box>

      {/* Rcuadro flotante */}
      {state.isOpen && (
        <Box className="floating-box animated-slide-in">
          {/* Cabecera verde */}
          <Box className="header-box">
            <WhatsAppIcon className="whatsapp-logo" />
            <Typography variant="h6" className="header-title">
              WhatsApp
            </Typography>
          </Box>

          {/* Input de nmero con mscara */}
          <InputMask
            mask="+999 999 999 9999"
            value={state.formData.phoneNumber}
            onChange={handlers.handlePhoneChange}
            maskChar="*">
            {(inputProps) => (
              <TextField
                {...inputProps}
                fullWidth
                label="Nmero de Telfono"
                variant="outlined"
                className="input-field"
              />
            )}
          </InputMask>

          {/* Input para mensaje */}
          <TextField
            fullWidth
            label="Mensaje"
            variant="outlined"
            multiline
            rows={4}
            name="message"
            value={state.formData.message}
            onChange={handlers.handleInputChange}
            className="input-field"
          />

          {/* Botn de enviar */}
          <Button
            variant="contained"
            fullWidth
            onClick={handlers.handleSubmit}
            disabled={!state.isPhoneComplete || !state.formData.message}
            sx={{
              backgroundColor: !state.formData.phoneNumber || !state.formData.message ? "gray" : "#25d366",
              color: "white",
              fontWeight: "bold",
              "&:hover": {
                backgroundColor: !state.formData.phoneNumber || !state.formData.message ? "gray" : "#1da851",
              },
            }}
          >
            Enviar
          </Button>
        </Box>
      )}
    </>
  )
}

export default WhatsApp
