import WhatsApp from "../Components/Whatsapp/Whatsapp"
import Footer from "../Components/Footer/Footer"

const DashboardLayout = () => {
  return (
    <div>
      <NavBar />
      <div>
        <Outlet />
      </div>
      <Footer />
      <WhatsApp />
    </div>
  )
}

export default DashboardLayout
