import React, { useState } from 'react'
import LocalShippingIcon from '@mui/icons-material/LocalShipping'
import CreditCardIcon from '@mui/icons-material/CreditCard'
import InventoryIcon from '@mui/icons-material/Inventory'

const useDashboard = () => {
    const [isLoading, setIsLoading] = useState(false)
    const [success, setSuccess] = useState(null)
    const [warning, setWarning] = useState(null)
    const [error, setError] = useState(null)
    const products = [1, 2, 3, 4, 5, 6]
    const business = [1, 2, 3, 4, 5, 6]

    const features = [
        {
          icon: <LocalShippingIcon style={{ fontSize: 100 }} />,
          title: "Aprovech el beneficio del envo gratis",
          description: "Aplica en compras a partir de $150,000. Sum todo lo que quieras al carrito.",
        },
        {
          icon: <CreditCardIcon style={{ fontSize: 100 }} />,
          title: "Eleg tu medio de pago favorito",
          description: "Pag con tarjeta o en efectivo. Tu dinero est protegido con Mercado Pago.",
        },
        {
          icon: <InventoryIcon style={{ fontSize: 100 }} />,
          title: "Recib tus productos en menos de 48 hs",
          description: "Tus paquetes estn seguros. Tens el respaldo de los envos con Mercado Libre.",
        },
    ]

    const state = {
      products,
      business,
      isLoading,
      setIsLoading,
      success,
      setSuccess,
      warning,
      setWarning,
      error,
      setError
    }
    
    return { state, features }
}

export default useDashboard