import { useState, useRef, useEffect } from 'react'
import { useTheme } from '@mui/material/styles'
import { useMediaQuery } from '@mui/material'
import { useUser } from '../../Context/Provider' 
import { Troubleshoot } from '@mui/icons-material'

const useFavorites = () => {
    const { dataContext } = useUser()
    const [anchorEl, setAnchorEl] = useState(null)
    const [selectedProduct, setSelectedProduct] = useState(null)
    const [selectedProductModal, setSelectedProductModal] = useState(null)
    const [page, setPage] = useState(0)
    const [rowsPerPage, setRowsPerPage] = useState(5)
    const [shouldScroll, setShouldScroll] = useState(false)
    const [openModal, setOpenModal] = useState(false)
    const theme = useTheme()
    const isMobile = useMediaQuery(theme.breakpoints.down('sm'))
    const tableRef = useRef(null)
    const handleOpenModal = () => setOpenModal(true)
    const handleCloseModal = () => setOpenModal(false)
    const scrollToTable = () => { tableRef.current && window.scrollTo({ top: 0, behavior: 'smooth' }) }

    const handleModalOpen = product => {
        setSelectedProductModal(product)
        setOpenModal(true)
    }

    const handleMenuOpen = (event, productId) => {
        setAnchorEl(event.currentTarget)
        setSelectedProduct(productId)
    }

    const handleMenuClose = () => {
        setAnchorEl(null)
        setSelectedProduct(null)
    }

    useEffect(() => { if (shouldScroll) {
        scrollToTable()
        setShouldScroll(false)
        }
    }, [shouldScroll])

    const handleChangePage = (event, newPage) => {
        event.preventDefault()
        setPage(newPage)
        setShouldScroll(true)
    }

    const handleChangeRowsPerPage = event => {
        event.preventDefault()
        setRowsPerPage(parseInt(event.target.value, 10))
        setPage(0)
        setShouldScroll(true)
    }

    const productosVisibles = dataContext.slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)

    const state = {
        anchorEl,
        selectedProduct,
        page,
        rowsPerPage,
        openModal,
        selectedProductModal
    }
    
    const handlers = {
        handleChangePage,
        handleChangeRowsPerPage,
        handleOpenModal,
        handleCloseModal,
        handleMenuClose,
        handleMenuOpen,
        setSelectedProductModal,
        handleModalOpen
    }

    const context = {
        dataContext,
        isMobile,
        tableRef,
        productosVisibles
    }

    return { state, handlers, context }
}

export default useFavorites